form{
    margin-top: 10px;
  }
input[type="text"] {
    height: 20px;
    outline: 2px solid #90ee90;
    width: 250px;
    border-radius: 5px;
    border: 0px;

}
input[type="text"]:focus {
    height: 20px;
    outline: 2px solid #90ee90;
    width: 250px;
    border-radius: 5px;
}
button {
    background-color: #90ee90;
    border: none;
    color: #fff;
    cursor: pointer;
    height: 26px;
    outline: none;
    width: 60px;
    border-radius: 5px;
    margin-left: -10px;
}